.login__submit-button {
  cursor: pointer;
  margin-top: 216px;
  padding: 14px;
  border: none;
  border-radius: 2px;
  background-color: #fff;
  transition: opacity .3s;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
